body {
    margin: 0;
}
.CMS {
    overflow: hidden;
    width: 100%;
}
.CMS .overlay {
    position: absolute;
    z-index: 99;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 1);
}
.CMS .overlay .content {
    margin: 15% auto;
    background-color: white;
    padding: 1rem;
    text-align: center;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    border-radius: 6px;
}
.CMS .overlay .content label {
    display: block;
}
.CMS .overlay .content .login {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    background-color: #1a73e8;
    padding: 0.5rem 1rem;
    border-radius: 4px;
    margin: auto;
    margin-top: 2rem;
    cursor: pointer;
    transition: all 125ms linear;
}
.CMS .overlay .content .login:hover {
    background-color: #3684eb;
}
.CMS .overlay .content .login span {
    color: white;
}
.CMS > .inner {
