    0%, 100% {
        -webkit-transform: scale(0);
   }
    50% {
        -webkit-transform: scale(1);
   }
}
@keyframes sk-bounce {
    0%, 100% {
        transform: scale(0);
        -webkit-transform: scale(0);
   }
    50% {
        transform: scale(1);
        -webkit-transform: scale(1);
   }
}
